body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
body .main {
  height: 100vh;
  display: flex;
  flex-direction: column;
}
body .main .content-body {
  flex: 1;
  position: relative;
  overflow: hidden;
}
body .full-screen-loading {
  position: fixed;
  height: 100vh;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.5);
}
body .full-screen-loading .am-activity-indicator-spinner {
  margin-left: calc(50% - 40px);
}
.app-menu.am-drawer {
  top: 45px;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.app-menu .am-drawer-sidebar {
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.app-menu .am-drawer-sidebar .am-list {
  width: 300px;
  padding: 0;
}
.app-menu.am-drawer-left .am-drawer-content {
  display: none;
}
.novel-body {
  position: relative;
  height: 100%;
  overflow: auto;
}
.novel-body .no-result-text {
  display: block;
  text-align: center;
  margin-top: 50%;
  opacity: 0.4;
}
.novel-body .back-icon {
  position: fixed;
  right: 5px;
  top: 50px;
  background: #fff;
  border-radius: 2px;
  text-align: center;
  z-index: 1;
}
.novel-body .chapter-section {
  transition: transform 0.2s linear;
  transform: translateX(0);
  position: absolute;
  background: #fff;
  width: 100%;
}
.novel-body .chapter-section.hide {
  transform: translateX(-100%);
}
.novel-body .chapter-section .am-list-item.active {
  background-color: rgba(16, 142, 233, 0.6);
}
.novel-body .chapter-section .am-list-item.active .am-list-content {
  color: #fff;
}
.novel-body .content-section {
  height: 100%;
  overflow: hidden;
  background: #d4ddcc;
  color: #4d443e;
  font-size: 16px;
}
.novel-body .content-title {
  padding: 0 10px;
}
.novel-body .content-paragraph {
  padding: 0 10px;
  line-height: 1.5;
  transform: translateY(0);
}
.novel-body .content-paragraph .chapter-title,
.novel-body .content-paragraph .main-content {
  margin: 0;
}
.novel-body .chapter-jump {
  margin: 0 20px;
}
.novel-body .chapter-jump .am-flexbox-item {
  text-align: center;
  margin: 10px 20px;
}



